* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background: #2f4749;
  background: -webkit-linear-gradient(to right, #467e85, #2f4749);
  background: linear-gradient(to right, #467e85, #2f4749);
  overflow-x: cover;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 100vw;
  height: 100vh;
}

h1 {
  text-shadow: 3px 0px 0px rgb(0, 0, 0);
  font-size: 40px;
  font-family: 'Finger Paint', cursive;
  cursor: pointer;
}

.color-pixel {
  height: 32px;
  width: 32px;
  cursor: pointer;
}

.pixel {
  height: 30px;
  width: 30px;
  border: none;
}

.pixels {
  display: grid;
  grid-template-columns: repeat(19, 28px);
  grid-template-rows: repeat(19, 28px);
  gap: 0px !important;
  border: none;
}


@media (max-width: 650px) {

  h1 {
    font-size: 30px;
  }

  .color-pixel {
    height: 27px;
    width: 27px;
  }

  .pixel {
    height: 24px;
    width: 24px;
  }

  .pixels {
    grid-template-columns: repeat(19, 24px);
    grid-template-rows: repeat(19, 24px);
  }

}

@media (max-width: 460px) {
  h1 {
    font-size: 25px;
  }

  .color-pixel {
    height: 23px;
    width: 23px;
  }

  .pixel {
    height: 20px;
    width: 20px;
  }

  .pixels {
    grid-template-columns: repeat(19, 20px);
    grid-template-rows: repeat(19, 20px);
  }
}

@media (max-width: 430px) {

  h1 {
    font-size: 25px;
  }

  .color-pixels{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 70%;
  }

  .color-pixel {
    height: 28px;
    width: 28px;
  }

  .pixel {
    height: 20px;
    width: 20px;
  }

  .pixels {
    grid-template-columns: repeat(19, 20px);
    grid-template-rows: repeat(19, 20px);
  }
}

@media (max-width: 400px) {

  h1{
    font-size: 24px;
  }

  .color-pixel {
    height: 23px;
    width: 23px;
  }

  .pixel {
    height: 18px;
    width: 18px;
  }

  .pixels {
    grid-template-columns: repeat(19, 18px);
    grid-template-rows: repeat(19, 18px);
  }
}

@media (max-width: 350px) {

  h1{
    font-size: 20px;
  }

  .pixel {
    height: 16px;
    width: 16px;
  }

  .pixels {
    grid-template-columns: repeat(19, 16px);
    grid-template-rows: repeat(19, 16px);
  }
}


